export const listaAcessorios = [
  'Traquias',
  'Blender',
  'Umidificador',
  'Jarra',
  'Vlvula exalatria',
  'Membrana',
  'Conector "Y"',
  'Conector de Dreno',
  'Sensor de Fluxo',
  'Sensor de temperatura',
  'Linha de presso proximal',
  'Pulmo de Teste',
  'Pedestal',
  'Brao articulado',
  'Mangueira de Oxignio (verde)',
  'Mangueira de Ar Medicinal (amarela)',
  'Cabo de Fora / Fonte',
];

export const Acessorio = (acessorio) => ({
  descricao: '',
  acompanha: false,
  quantidade: 0,
  estado_de_conservacao: '',
  ...acessorio,
});

export const listaFormAcessorios = (acessorios) => listaAcessorios
  .sort((a, b) => a.localeCompare(b))
  .map((item) => Acessorio(acessorios
    .find((acessorio) => acessorio.descricao === item) || { descricao: item }));
